import axios from 'axios'
const baseURI = 'http://47.105.118.135:8080/pano/%folder%/%dir%.jpg'
const panoParamsURI = 'http://47.105.118.135:8080/pano_upload.php'
const dirs = ["l", "r", "u", "d", "b", "f"]

let imgData
let _currentScene

export function getPanoParams(userId) {
    return new Promise((resolve,reject) => {
        axios.get(`${panoParamsURI}?userid=${userId}`).then((data) => {
            imgData = data.data.points
            resolve(imgData)
        })
    })
}


export function updateTextures(index, dir = 'next') {

    if (dir === 'prev') {
        index--
        if (!imgData[index].img) index = imgData.length - 1
    } else {
        index++
        if (!imgData[index]) index = 0
    }

    let returnArr = []
    returnArr = dirs.map((v) => {
        let str = baseURI.replace('%dir%', v).replace('%folder%', imgData[index].img)
        return str
    })
    let radius = imgData.map((v) => {
        return v.end / 360 * Math.PI * 2
    })
    let hotSpots = imgData.map((v) =>{
        return v.frames.map((hotspot) =>{
            let data = {
                media: hotspot.media,
                pos: hotspot.pos.replace('(', '').replace(')', '').split(',')
            }
            return data
        })
        
    })
    _currentScene = {
        urls: returnArr,
        index,
        radius: radius[index],
        hotSpots: hotSpots[index]
    }
    return _currentScene
}
export function currentScene () {
    return _currentScene
}
export function textureCount() { return imgData.length }


